.fa,
.fa-brands,
.fa-classic,
.fa-regular,
.fa-sharp,
.fa-solid,
.fab,
.far,
.fas {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  display: var(--fa-display, inline-block);
  font-style: normal;
  font-variant: normal;
  line-height: 1;
  text-rendering: auto;
}

.fa-classic,
.fa-regular,
.fa-solid,
.far,
.fas {
  font-family: "Font Awesome 6 Free";
}
.fa-bars:before,
.fa-navicon:before {
  content: "\f0c9";
}

:host,
:root {
  --fa-font-regular: normal 400 1em/1 "Font Awesome 6 Free";
}

@font-face {
  font-family: "Font Awesome 6 Free";
  font-style: normal;
  font-weight: 400;
  font-display: block;
  src: url(../fonts/files/fa-solid-900.woff2) format("woff2");
}

.fa-regular,
.far {
  font-weight: 400;
}

:host,
:root {
  --fa-style-family-classic: "Font Awesome 6 Free";
  --fa-font-solid: normal 900 1em/1 "Font Awesome 6 Free";
}

@font-face {
  font-family: "Font Awesome 6 Free";
  font-style: normal;
  font-weight: 900;
  font-display: block;
  src: url(../fonts/files/fa-solid-900.woff2) format("woff2");
}

.fa-solid,
.fas {
  font-weight: 900;
}
@font-face {
  font-family: "Font Awesome 5 Free";
  font-display: block;
  font-weight: 900;
  src: url(../fonts/files/fa-solid-900.woff2) format("woff2");
}
@font-face {
  font-family: "FontAwesome";
  font-display: block;
  src: url(../fonts/files/fa-solid-900.woff2) format("woff2");
}

