.container {
  position: fixed;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.title {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.7em;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.content {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.1em;
  text-align: center;
  max-width: 50em;
}
.content > p > a {
  color: #a9d1ff;
  text-decoration: none;
}
.content > p > a:hover {
  color: #85beff;
  text-decoration: underline;
}
.underline {
  display: block;
  height: 0.1em;
  margin: 0 auto;
  width: 0;
  transition: width 0.25s linear;
}

*:hover > .underline,
.active > .underline {
  width: 100%;
}
